import React from 'react';
import { ArrowTrendingUpIcon, BanknotesIcon, CurrencyDollarIcon, ShieldExclamationIcon } from '@heroicons/react/24/outline';
import { BarChart, Bar, XAxis, ResponsiveContainer } from 'recharts';

const MOCK_NET_WORTH = {
  total: 12734521,
  cryptoassets: 3245678,
  investments: 3126543,
  reserves: 1567320,
  liabilities: -205020,
};

const items = [
  {
    label: 'Cryptoassets',
    value: MOCK_NET_WORTH.cryptoassets,
    icon: <ArrowTrendingUpIcon className="w-5 h-5 text-indigo-500" />,
    color: '#6366f1',
  },
  {
    label: 'Traditional Investments',
    value: MOCK_NET_WORTH.investments,
    icon: <BanknotesIcon className="w-5 h-5 text-green-500" />,
    color: '#10b981',
  },
  {
    label: 'Cash Reserves',
    value: MOCK_NET_WORTH.reserves,
    icon: <CurrencyDollarIcon className="w-5 h-5 text-yellow-500" />,
    color: '#f59e42',
  },
  {
    label: 'Liabilities',
    value: Math.abs(MOCK_NET_WORTH.liabilities),
    icon: <ShieldExclamationIcon className="w-5 h-5 text-red-500" />,
    color: '#f43f5e',
    negative: true,
  },
];

export default function NetWorthOverview() {
  const maxValue = Math.max(...items.map(i => i.value));
  return (
    <div className="bg-white rounded-2xl shadow-lg border border-gray-100 p-8 flex flex-col gap-6">
      <h2 className="text-lg font-bold text-gray-900 tracking-tight mb-2">Net Worth Overview</h2>
      <div className="flex flex-col sm:flex-row sm:items-end gap-4 mb-2">
        <CurrencyDollarIcon className="w-10 h-10 text-blue-500 bg-blue-100 rounded-full p-2 shadow-sm" />
        <span className="text-4xl sm:text-5xl font-extrabold text-gray-900 tracking-tight drop-shadow-sm leading-tight">${MOCK_NET_WORTH.total.toLocaleString()}</span>
      </div>
      <div className="flex flex-col gap-4">
        {items.map((item, idx) => (
          <div key={item.label} className="flex items-center gap-4">
            <span>{item.icon}</span>
            <span className="text-sm font-medium text-gray-700 w-40 min-w-[120px]">{item.label}</span>
            <div className="flex-1 flex items-center">
              <ResponsiveContainer width="100%" height={18}>
                <BarChart
                  data={[{ value: item.value }]}
                  layout="vertical"
                  margin={{ left: 0, right: 0, top: 0, bottom: 0 }}
                  barCategoryGap={0}
                >
                  <XAxis type="number" hide domain={[0, maxValue]} />
                  <Bar
                    dataKey="value"
                    fill={item.color}
                    radius={[6, 6, 6, 6]}
                    background={{ fill: '#f3f4f6' }}
                    barSize={item.negative ? 12 : 16}
                  />
                </BarChart>
              </ResponsiveContainer>
            </div>
            <span className={`text-base font-semibold tabular-nums ${item.negative ? 'text-red-600' : 'text-gray-900'}`}>{item.negative ? '-' : ''}${item.value.toLocaleString()}</span>
          </div>
        ))}
      </div>
    </div>
  );
}
